body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0 auto;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 10px; }
  .container--header {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  .container--slider {
    position: relative;
    max-width: 1248px;
    padding: 0; }
  .container--catalog {
    display: flex;
    align-items: flex-start; }

footer {
  margin-top: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  padding-bottom: 62px;
  background-color: #dcdcdc; }

.header__logo {
  margin-top: 65px; }

.header__contacts {
  margin-top: 36px; }

.contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-end; }

.contacts__wrapper {
  margin: 0; }

.contacts__link {
  font-family: "Open Sans Condensed", "Arial", sans-serif;
  font-size: 40px;
  line-height: 46px;
  letter-spacing: 0.025em;
  color: #767676; }
  .contacts__link:hover, .contacts__link:focus {
    opacity: 0.8; }
  .contacts__link:active {
    opacity: 0.6; }

.contacts__callback-btn {
  display: block;
  margin: 3px 0 0 0;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer; }
  .contacts__callback-btn span {
    font-size: 24px;
    font-weight: 700;
    letter-spacing: 0.025em;
    text-decoration: underline dashed;
    color: #767676; }
  .contacts__callback-btn:hover span, .contacts__callback-btn:focus span {
    opacity: 0.8; }
  .contacts__callback-btn:active span {
    opacity: 0.6; }

.main__navigation {
  background-color: #777777; }

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }

.site-list__item {
  position: relative;
  margin-right: 40px;
  padding-top: 19px;
  padding-bottom: 27px; }
  .site-list__item:last-child {
    margin-right: 0; }
    .site-list__item:last-child::after {
      display: none; }
  .site-list__item::after {
    content: "";
    position: absolute;
    top: 25px;
    right: -20px;
    width: 2px;
    height: 25px;
    background-color: #ffffff; }

.site-list__link {
  font-size: 30px;
  font-weight: 400;
  letter-spacing: 0.025em;
  color: #ffffff; }
  .site-list__link:hover, .site-list__link:focus {
    text-decoration: underline; }
  .site-list__link:active {
    opacity: 0.8; }

.slider {
  background-color: #f9f9f9; }

.slider__container {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  height: 400px; }

.slider__item {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  transition: opacity 2s; }
  .slider__item--active {
    opacity: 1; }

.slider__image {
  width: 100%;
  height: 400px;
  object-fit: cover; }

.slider__controls {
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: 0 auto;
  padding: 0 10px;
  transform: translate(-50%, -50%); }

.slider__btn {
  display: block;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer; }

.breadcrumbs {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.breadcrumbs__item {
  position: relative;
  margin-right: 20px;
  padding-top: 5px;
  padding-bottom: 5px; }
  .breadcrumbs__item:last-child {
    margin-right: 0; }
    .breadcrumbs__item:last-child::after {
      display: none; }
  .breadcrumbs__item::after {
    content: "/";
    position: absolute;
    top: 5px;
    right: -10px;
    color: #767676; }

.breadcrumbs__link {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.025em;
  color: #767676;
  text-decoration: underline; }
  .breadcrumbs__link--current {
    text-decoration: none; }
  .breadcrumbs__link:hover, .breadcrumbs__link:focus {
    opacity: 0.8; }
  .breadcrumbs__link:active {
    opacity: 0.6; }

.catalog__aside {
  flex-shrink: 0;
  width: 200px;
  padding-top: 13px; }

.catalog__navigation-list {
  margin-bottom: 103px; }

.catalog__price {
  margin-top: 103px; }

.catalog__main {
  margin-top: 21px; }

.navigation {
  margin: 0;
  padding: 0;
  list-style: none; }

.navigation__sublist {
  margin: 0;
  padding: 0;
  list-style: none;
  transform: translateY(-3px); }

.navigation__link {
  font-size: 18px;
  line-height: 30px;
  letter-spacing: 0.025em;
  color: #767676; }
  .navigation__link--active {
    text-decoration: underline; }
  .navigation__link:hover, .navigation__link:focus {
    text-decoration: underline; }
  .navigation__link:active {
    opacity: 0.8; }

.navigation__subitem {
  position: relative;
  padding-left: 19px; }
  .navigation__subitem::before {
    content: "";
    position: absolute;
    top: 9px;
    left: 9px;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: #767676; }

.navigation__sublink {
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.025em;
  color: #767676; }
  .navigation__sublink:hover, .navigation__sublink:focus {
    border-bottom: 1px solid #767676;
    color: #010101; }
  .navigation__sublink:active {
    opacity: 0.8; }

.price {
  text-align: center; }

.price__image {
  display: block;
  margin: 0 auto; }

.price__link {
  font-size: 30px;
  font-weight: 700;
  letter-spacing: 0.025em;
  color: #444444; }
  .price__link:hover, .price__link:focus {
    text-decoration: underline; }
  .price__link:active {
    opacity: 0.8; }

.goods {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  display: -ms-grid;
  grid-template-columns: auto auto auto;
  -ms-grid-columns: auto 15px auto 15px auto;
  gap: 15px; }
  @media (max-width: 1000px) {
    .goods {
      grid-template-columns: auto auto;
      -ms-grid-columns: auto 15px auto; } }
  @media (max-width: 850px) {
    .goods {
      grid-template-columns: auto;
      -ms-grid-columns: auto; } }

.goods__item {
  margin-bottom: 5px; }

.card {
  display: grid;
  display: -ms-grid;
  grid-template-columns: 50% 50%;
  -ms-grid-columns: auto auto;
  grid-template-rows: auto auto auto;
  -ms-grid-rows: auto auto auto;
  width: 250px;
  padding: 20px 20px 14px;
  background-color: #dcdcdc; }
  .card picture {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    -ms-grid-column: 1;
    -ms-grid-column-span: 3;
    -ms-grid-row: 1;
    margin: 0 0 18px;
    line-height: 0; }

.card__title {
  grid-column: 1 / 3;
  grid-row: 2 / 3;
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  -ms-grid-row: 2;
  margin: 0 0 47px;
  font-size: 18px;
  font-weight: 400;
  letter-spacing: 0.025em;
  color: #767676; }

.card__price {
  margin: 0;
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 0.025em;
  color: #444444; }

.card__order-btn {
  width: 28px;
  height: 21px;
  margin: 8px 0 0 61px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer; }
